.paginator {

    margin:1em;
    }

.pageNumber{
    border-radius: 0 10px;
    padding: 0.3em;
    margin: 0.3em;
    /*border: 1px solid grey;*/
    color: #fffffc;
    background-color: #1969ca;
    }
.pageNumber.selectedPage{
    font-weight: bold;
    border-color: black;
}

.button{
    display: inline-block;
    color: #1969ca;
    font-weight: 700;
    text-decoration: none;
    user-select: none;
    padding: .5em 2em;
    outline: none;
    border: 2px solid;
    border-radius: 0 10px;
    transition: 0.2s;
    box-shadow: 0 1px 2px rgba(0,0,0,.2);
}
.button:hover { background: rgba(255,255,255,.2);
    box-shadow: 0 1px 2px rgba(0,0,0,.3);
}

.button:active { background: #c3c3db;
    box-shadow: 0 1px 2px rgba(0,0,0,.6) inset;
}